@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root
{
  --red:#d01c28;
  --gray:#2e2e2e;
}
*
{
  margin: 0;
}
.container
{
  max-width: 1280px;
  margin: 0 auto;
 
}
body
{
  font-family: "Poppins",sans-serif;
}
.hero h1{
  text-transform: uppercase;
  font-size: 108px;
  font-weight: 800;
  line-height: 102px;
}
nav{
  display: flex;
  justify-content: space-between;
  height: 72px;
  text-transform: uppercase;
  align-items: center;
}
nav ul{
  display: flex;
  gap: 24px;
    list-style: none;
}
button
{
  all: unset;
  padding: 6px 16px;
  background: var(--red);
  height: 32px;
  color: white;
  font-weight: 500;
}
.hero{
  display: flex;
  align-items: center;
  height: calc(100vh-72px);
}
.hero img {
  height: auto;
  display: block;
}
.hero-content
{
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.hero-btn 
{
  display: flex;
  gap: 40px;
 
}
.hero-btn button{
  font-size: 24px;
}
.hero p
{
  max-width: 400px;
  font-weight: 600;
  color: var(gray);
}
.secondary-btn
{
  background: transparent;
  color: var(--gray);
  border: 1px solid gray;
}
.shopping p{
  font-size: 14px;
  margin-bottom: 8px;
}
.brand-icons
{
  display: flex;
  gap: 16px;
}

@media screen and (max-width: 768px) {
  .hero h1 {
    font-size: 48px;
    line-height: 58px;
  }
  .hero {
    height: auto;
    padding: 50px 20px;
  }
  .hero-content {
    text-align: center;
  }
  .hero-btn {
    justify-content: center;
  }
  .hero-btn button {
    font-size: 20px;
    padding: 10px 20px;
  }
  .hero p {
    max-width: 100%;
    text-align: center;
  }
  
}
@media screen and (max-width: 480px) {
  .hero h1 {
    font-size: 36px;
    line-height: 46px;
  }
    .hero img
    {
      max-width: 100%;
    }
     
}